*,
*:before,
*:after {
  box-sizing: border-box;
}
body
{
width:100%;
height:100%;
color: #161b1e;
font-family: "Courier New", Courier, monospace;
background-image: url("bg.jpg");
background-size: cover;
background-position:50% 50%;
}
.container
{ 
  opacity: 1;
 box-sizing: border-box;
 position:relative;
  top:50%;
  left:20%;
  width:60%;
  height:70%;
  padding:10px 10px;
  background-color: "#FFFFFF";
  border: 1px solid black;
  border-radius:25px;
  box-shadow: 10px 10px 5px grey;
  
}



.image
{
  width:10%;
  height:10%;
  display: block;
  float: left;
}



.headline
{ 
  margin:3%;
  text-align:center;
  color:white;
  display:block;
  text-transform:uppercase;
}
.powerful-intro
{
  text-align: center;
  color:white;
  padding:2px;
  margin:1px;
}

.sectionsright
{
 width:200px;
 height:40px;
 display:block;
 margin:0;
 background-color:teal;
 border-radius:30%;
 position: relative ;
 right:"40%";
}

.sectionsleft
{
   margin:0;
  width:200px;
 height: 40px;
 display:block;
 background-color:teal;
 border-radius:30%;
 float:left;
}

